<template>
  <div class="card">
    <div class="card-header">
      View Contact
    </div>
    <div class="card-body">
      <h4>{{ contact.firstname }} {{ contact.lastname }}</h4>
      <h5>Email: {{ contact.emailaddress }}</h5>
      <h5>Phone number: {{ contact.phonenumber }}</h5>
      <router-link class="btn btn-info" :to="`/edit/${contact.slug}`">Edit Contact</router-link>
      <button class="btn btn-danger">Delete Contact</button>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  data() {
    return {
      contact: {}
    }
  },
  created() {
    let slug = this.$router.history.current.params.person_slug
    let doc = db.collection('contact')
    doc.where('slug', '==', slug ).get().then(e => {
      e.forEach(e => {
        this.contact = e.data()
      });
    })
  }
}
</script>



