<template>
  <form @submit.prevent="saveContact">
    <div class="form-group">
      <input type="input" class="form-control" placeholder="First Name" v-model="firstname" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Last Name" v-model="lastname" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Email" v-model="emailaddress" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Phone Number" v-model="phonenumber" required>
    </div>
    <button type="submit" class="btn btn-primary btn-round btn-block">Save Contact</button>
  </form>
</template>
<script>
  import db from './firebaseInit'
  export default {
    name: 'new-contact',
    data() {
      return {
        firstname: null,
        lastname: null,
        emailaddress: null,
        phonenumber: null
      }
    },
    methods: {
      saveContact() {
        db.collection('contact').add({
          firstname: this.firstname,
          lastname: this.lastname,
          emailaddress: this.emailaddress,
          phonenumber: this.phonenumber,
          slug: this.generateUUID()
        }).then(e => {
          console.log('Document Written with ID: ', e.id)
        }).catch(err => {
          console.error('Error adding document: ', err)
        })
      },
      generateUUID() {
        let d = new Date().getTime()
        let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
          let r = (d + Math.random() * 16) % 16 | 0
          d = Math.floor(d / 16)
          return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
        })
        return uuid
      }
    }
  }
</script>


