<template>
  <form @submit.prevent="saveContact">
    <div class="form-group">
      <input type="input" class="form-control" placeholder="First Name" v-model="firstname" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Last Name" v-model="lastname" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Email" v-model="emailaddress" required>
    </div>
    <div class="form-group">
      <input type="input" class="form-control" placeholder="Phone Number" v-model="phonenumber" required>
    </div>
    <button type="submit" class="btn btn-primary btn-round btn-block">Update Contact</button>
  </form>
</template>
<script>
  import db from './firebaseInit'
  export default {
    name: 'edit-contact',
    data() {
      return {
        firstname: null,
        lastname: null,
        emailaddress: null,
        phonenumber: null
      }
    },
    methods: {
      saveContact() {
        let cr = this;
        let slug = this.$router.history.current.params.person_slug
        db.collection('contact').where('slug', '==', slug).get().then(e => {
          let id = e.docs[0].id
          let docs = db.collection('contact').doc(id)
          db.runTransaction(function(transaction) {
            return transaction.get(docs).then(function(sfdoc) {
              if (sfdoc.exists) {
                transaction.update(docs, {
                  'firstname': cr.firstname,
                  'lastname': cr.lastname,
                  'emailaddress': cr.emailaddress,
                  'phonenumber': cr.phonenumber
                })
              }
            })
          })
        })
      }
    },
    created() {
      let slug = this.$router.history.current.params.person_slug
      let doc = db.collection('contact').where('slug', '==', slug).get().then(e => {
        e.forEach(e => {
          this.firstname = e.data().firstname
          this.lastname = e.data().lastname
          this.emailaddress = e.data().emailaddress
          this.phonenumber = e.data().phonenumber
        })
      })
      this.saveContact()
    }
  }
</script>


