<template>
  <table class="table table-borderless">
    <thead>
      <th scope="col">#</th>
      <th scope="col">First Name</th>
      <th scope="col">Last Name</th>
      <th scope="col">Email</th>
      <th scope="col">Phone Number</th>
    </thead>
    <tbody>
      <tr v-for="(e, i) in contacts" :key="e.idContact" @click="trClick(e.slug)">
        <td>{{i+1}}</td>
        <td>{{ e.firstname }}</td>
        <td>{{ e.lastname }}</td>
        <td>{{ e.emailaddress }}</td>
        <td>{{ e.phonenumber }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
  import db from './firebaseInit'
  export default {
    data() {
      return {
        contacts: []
      }
    },
    name: 'Home',
    created() {
      let contact = db.collection('contact')
      contact.get().then(e => {
        e.forEach(element => {
          let dd = {
            idContact: element.id,
            firstname: element.data().firstname,
            lastname: element.data().lastname,
            emailaddress: element.data().emailaddress,
            phonenumber: element.data().phonenumber,
            slug: element.data().slug
          }
          this.contacts.push(dd)
        })
      })
    },
    methods: {
      trClick(e) {
        window.location = '#/' + e
      }
    }
  }
</script>

<style lang="postcss" scoped>
table > tbody > tr:hover {
  background: #00c9a7;
  cursor: pointer;
  color: #fff;
}
</style>



